import { doc, getDoc } from "firebase/firestore";
import { useEffect, useState } from "react";
import { db } from "../../../configs/firebase.config";
import { useAppSelector } from "../../../hooks/useRedux";

const VerifyUser = () => {

  const [account, setAccount] = useState<boolean>(false)
  const uid = useAppSelector(state => state.auth.uid);

  const getAccount = async () => {

    if (uid) {
      const docRef = await doc(db, "users", uid);
      const docSnap = await getDoc(docRef);
      if (docSnap.exists()) {
        console.log(docSnap.data());
        if (docSnap.data()?.address && docSnap.data()?.cmnd && docSnap.data()?.dateOfBirth && docSnap.data()?.fullName && docSnap.data()?.genre && docSnap.data()?.image && docSnap.data()?.income && docSnap.data()?.job && docSnap.data()?.nameAccount && docSnap.data()?.nameBank && docSnap.data()?.numberBank && docSnap.data()?.phoneNumber && docSnap.data()?.photoURL && docSnap.data()?.purpose && docSnap.data()?.relationship) {
          setAccount(true);
        }

      } else {
        setAccount(false);
      }
    }
  }

  useEffect(() => {
    getAccount();
  }, [account]);

  if (account) {
    return null;
  }

  return (
    <div className="bg-[rgba(0,106,176,0.14)] px-[17px] py-[4px] mb-[20px]">
      <p className="py-3 border-b-[0.5px] border-[rgba(0,106,176,0.3)] text-left">
        Xác thực tài khoản
      </p>
      <div className="flex items-center justify-center px-[10px] py-[30px]" >
        <span className="min-w-[30%]">
          <svg
            aria-hidden="true"
            focusable="false"
            data-prefix="fas"
            data-icon="bell-concierge"
            className="svg-inline--fa fa-bell-concierge w-[45px] m-auto h-[45px] text-[#242ba6]"
            role="img"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 512 512"
          >
            <path
              fill="currentColor"
              d="M216 64c-13.3 0-24 10.7-24 24s10.7 24 24 24h16v33.3C119.6 157.2 32 252.4 32 368H480c0-115.6-87.6-210.8-200-222.7V112h16c13.3 0 24-10.7 24-24s-10.7-24-24-24H256 216zM24 400c-13.3 0-24 10.7-24 24s10.7 24 24 24H488c13.3 0 24-10.7 24-24s-10.7-24-24-24H24z"
            ></path>
          </svg>
        </span>
        <p className="text-left">Bổ sung CMND/CCCD và chân dung để hoàn tất định danh</p>
      </div>
      <p className="text-right mb-[5px]"><a className=" text-[#242ba6] text-[17px] font-bold" href="/user/accuracy">Xác thực ngay</a></p>
    </div>
  );
};

export default VerifyUser;
